import React from 'react'
import Helmet from 'react-helmet'

import Layout from '../components/layout'
import HeaderGeneric from '../components/HeaderGeneric'
import pic04 from '../assets/images/pic04.jpg'

class Activities extends React.Component {
  render() {

    return (
      <Layout>
        <Helmet title="الهیكل التنظیمي العام للأنشطة" />
        <HeaderGeneric />
        <div id="main">
          <section id="content" className="main special">
            <span className="image main"><img src={pic04} alt="" /></span>
            <header className="major">
              <h1>الهیكل التنظیمي العام للأنشطة</h1>
            </header>
            <span className="icon style2 fa-check"></span>

            <h2>الرعایة الصحیة</h2>
            <p>تقدم مؤسسة شباب بیحب الخیر الرعایة الصحیة للأشخاص الذین یحتاجون إلیها في حالات الطوارئ والمواقف
الیومیة. و تقوم على بناء قدرات المجتمعات المحلیة لمنع ظهور المرض وتوفیر الأدویة وغیرها من الإمدادات
المنقذة للحیاة من خلال برامج العلاج. ونجحت المؤسسة في إطلاق عدة قوافل طبیة بالقاهرة الكبري والمحافظات
.بهدف إیصال ادویة و الكشف الطبي و التحالیل و برامج التوعیة الصحیة للمناطق الأكثر احتیاجا</p>
            <span className="icon style2 fa-check"></span>
            <h2>رعایة الطفولة المبكرة</h2>
            <p>تعمل مؤسسة شباب بیحب الخیر على إعداد الفتیان والفتیات الصغار لمستقبل ناجح ، من خلال تحسین الوصول إلى
خدمات رعایة الطفولة المبكرة والتنمیة الجیدة. تساعد برامج المؤسسة في بناء المهارات الاجتماعیة والعاطفیة
والمعرفیة والجسدیة واللغویة والحركیة التي یحتاجها الأطفال الذین تتراوح أعمارهم بین 5-7 سنوات للنمو
والازدهار في مراحل لاحقة من الحیاة. تتبنى المؤسسة نهج بناء الشراكات مع مؤسسات الدولة والمنظمات
7
المجتمعیة والدولیة لتوسیع خدمات تنمیة الطفولة المبكرة وبناء مهارات المعلمین والمیسرین وإشراك أولیاء الأمور
في هذه المرحلة الحرجة من التعلیم</p>
            <span className="icon style2 fa-check"></span>
            <h2>تنمیة المراهقین والشباب </h2>
            <p>تعمل مؤسسة شباب بیحب الخیر على توفیر برامج إعداد الشباب حتى یتمكنوا من المساهمة بنشاط في رفاهیتهم و
فرصهم المستقبلیة الإیجابیة والمجتمع ككل. تعمل برامجنا من خلال المجالس الاستشاریة الشبابیة و تدریب
المتطوعین على بناء المرونة الشخصیة وخلق فرص التعلم لدعم المراهقین والمراهقین والأولاد والشباب للاستفادة
من الخیارات الآمنة والعادلة ، ویكون لهم صوت مؤثر في صنع القرار في حیاتهم وعلى نطاق أوسع في
المجتمعات. ویشمل ذلك دعم الشباب لتعزیز مهاراتهم والاستفادة من مختلف سبل العیش الآمنة ، مما یؤدي إلى
تحسین الوضع الاقتصادي لهم ولأسرهم. تسهل البرامج المشاركة الاجتماعیة والمشاركة المجتمعیة ، لتشجیع
الفتیات والفتیان على التعاون كعوامل للتغییر في مجتمعاتهم</p>
            <span className="icon style2 fa-check"></span>
            <h2>التعلیم الاساسي </h2>
            <p>تعمل مؤسسة شباب بیحب الخیر على توفیر وتحسین تجربة التعلم والنجاح للبنین والبنات من خلال بناء فرص
تعلیمیة عالیة الجودة. من خلال دعمنا أهداف السیاسة التعلیمیة لمصر ونسعى للعمل جنبًا إلى جنب مع وزارة
التربیة والتعلیم ومجموعات المجتمع للمساهمة في تعلیم أفضل للجمیع وتعزیز بیئات التعلم الآمنة، وإمكانیة الوصول
إلى تعلیم رسمي وغیر رسمي عالي الجودة یستهدف المتسربین من المدارس والأطفال اللاجئین (مدارس مجتمعیة -
حضانات - فصول مجتمعیة).
ً من خلال برامج المؤسسة نعمل على تحسین مهارات القراءة والكتابة للأطفال. تشمل برامج بناء القدرات لدینا أیضا
برامج الصحة المدرسیة والتغذیة ، بالإضافة إلى برامج لتحسین مشاركة أولیاء الأمور وصوت الأطفال في
المدارس.و بناء مهارات في أسالیب وأنظمة التأدیب الإیجابي وحمایة الطفل للحد من العنف في المدارس ، وبناء
القدرات لتحسین نتائج التعلم للفتیات والفتیان، وكذلك الأطفال ذوي الإعاقة</p>
            <span className="icon style2 fa-check"></span>
            <h2>اللاجئین والمهاجرین </h2>
            <p>تدعم مؤسسة شباب بیحب الخیر اللاجئین والمجتمعات المضیفة لتحسین دخولهم ، وتستهدف بشكل خاص الشباب
والنساء المهمشین في القاهرة الكبرى. تضمن البرامج أن یكون المصریون واللاجئون والمهاجرون من الشباب
والنساء مجهزین بشكل أفضل ومطلعین ومتصلین بأسواق العمل المحلیة ، والمشاركة مع أصحاب العمل والحصول
ً على فرص عمل بأجر آمن أو بدلا من ذلك تكون قادرة على إقامة أنشطة مدرة للدخل في شكل أعمال تجاریة
صغیرة</p>

            <span className="icon style2 fa-check"></span>
            <h2>الأطفال والشباب غیر المصحوبین</h2>
            <p>تقوم مؤسسة شباب بیحب الخیر على مساعدة الأطفال والشباب غیر المصحوبین بذویهم الذین یسعون للحصول على
اللجوء أو منحهم وضع اللاجئ في مصر. یحدد فریق المؤسسة الأطفال المعرضین للخطر وضحایا سوء المعاملة.
تسعي المؤسسة على العمل مع لجان تحدید مصالح الطفل الفضلى في مفوضیة الأمم المتحدة السامیة لشؤون
8
اللاجئین ؛ بالتنسیق مع المنظمات غیر الحكومیة والمؤسسات الأخرى المهتمة بالحمایة والرعایة الاجتماعیة
للأطفال والشباب غیر المصحوبین بذویهم</p>
            
            <span className="icon style2 fa-check"></span>
            <h2>برامج تمكین المرأة</h2>
            <p>تتبنى المؤسسة مبدأ تمكین المرأة كهدف رئیسي في برامجها ومشاریعها التنمویة. حیث یعتبر التمكین أكثر المفاهیم
ً المرأة كعنصر فعال في تنمیة وتطور المجتمع. ویهدف الى إیجاد سیاق تنموي ملائم لمشاركة المرأة في
اعترافا
البرامج والمشاریع التنمویة. ویعتمد هذا المبدأ على تطویر مهارات وقدرات المرأة، بالإضافة الى تطویر علاقاتها
ًالاجتماعیة ودعمها باتجاه یزید من قدرتها على الاستقرار والتوازن. و رفع مستوى وعي المرأة نفسیا

واقتصادیاً واجتماعیا
ً لتتمكن من وقایة نفسها من العنف الأسري
</p>

            <span className="icon style2 fa-check"></span>
            <h2>مناهضة الاتجار بالبشر</h2>
            <p>تسعى المؤسسة على خلق فریق طوارئ مؤهل للعمل على نشر التوعیة و تحدید الضحایا في المجتمعات اللاجئة,
من خلال برنامج علمي قائم على مناهضة كافة أشكال الاتجار بالبشر, ومساعدة الضحایا في الوصول إلى الحمایة
القانونیة و المؤسسیة من خلال الدولة و المنظمات التي تعمل على مساعدة ضحایا الاتجار بالبشر</p>
            
            <span className="icon style2 fa-check"></span>
            <h2>مناهضة العنف القائم على النوع الاجتماعي</h2>
            <p>تسعى المؤسسة على مساعدة ضحایا العنف القائم على النوع الاجتماعي من المجتمع المضیف و طالبي اللجوء, من
خلال حملات توعیة ومبادرات مساعدة الناجیات وإرشادهم الى المنظمات والمؤسسات التي تقدم
المساعدات الطبیة و القانونیة</p>
            
            <span className="icon style2 fa-check"></span>
            <h2>الأنشطة الثقافیة</h2>
            <p>تقوم على تنفیذ الانشطة الثقافیة و والندوات التي تسهم إلى إحلال السلام المجتمعي, و حملات التوعیة الثقافیة و
الصداقات بین الشعوب و العیش السلمي بین المجتمعات المضیفة و اللاجئة</p>
            
            <span className="icon style2 fa-check"></span>         
            <h2>إعادة تدویر وحفاظ على البیئة</h2>
            <p></p>
            <span className="icon style2 fa-check"></span>
            <h2>المساعدات الاجتماعیة</h2>
            <p></p>

          </section>
        </div>
      </Layout>
    )
  }
}

export default Activities
